import React, { Fragment } from 'react';

import Layout from '../components/layout';
import SignInForm, {
  SignInGoogle,
  SignInFacebook,
  SignInTwitter,
} from '../components/SignIn';
import { SignUpLink } from '../components/SignUp';
import { PasswordForgetLink } from '../components/PasswordForget';

const SignInPage = () => (
  <Fragment>
    <h1>SignIn..</h1>
    <SignInForm />
    <SignInGoogle />
    <SignInFacebook />
    <SignInTwitter />
    <PasswordForgetLink />
    <SignUpLink />
  </Fragment>
);

exports.onRenderBody = ({ setPostBodyComponents }) => {
  setPostBodyComponents([
    <div dangerouslySetInnerHTML={{ __html: `<div><h1>abc</h1></div>` }}/>
  ])
};

export default () => (
  <Layout>
    <SignInPage />
  </Layout>
);
